$button__border-radius: 30px;

.button {
  position: relative;

  display: flex;
  align-items: center;
  gap: 20px;

  border-radius: $button__border-radius;
  background-color: $color-white;
  transition: background-color $trans-default;
  outline: none;

  border: 1px solid $color-cerulean-blue;
  margin: 0;
  padding: 4px;
  padding-left: 20px;

  @include hover-focus {
    background-color: $color-cerulean-blue;

    .button__text {
      color: $color-white;
    }

    .button__icon {
      color: $color-cerulean-blue;

      background-color: $color-white;
      transform: rotate(45deg);
    }
  }

  @include active {
    background-color: $color-cerulean-blue;

    .button__text {
      color: rgba($color-white, 0.7);
    }

    .button__icon {
      color: rgba($color-cerulean-blue, 0.7);

      transform: rotate(45deg);
      background-color: $color-white;
    }
  }

  @include disabled {
    cursor: not-allowed;
    border-color: $color-disabled-default;

    .button__text {
      color: $color-disabled-default;
    }

    .button__icon {
      color: $color-white;

      background-color: $color-disabled-default;
    }
  }
}

.button--light {
  border-color: $color-white;
  background-color: $color-cerulean-blue;

  .button__text {
    color: $color-white;
  }

  .button__icon {
    color: $color-cerulean-blue;

    background-color: $color-white;
  }

  @include hover-focus {
    background-color: $color-white;

    .button__text {
      color: $color-cerulean-blue;
    }

    .button__icon {
      color: $color-white;

      background-color: $color-cerulean-blue;
    }
  }

  @include active {
    background-color: $color-white;

    .button__text {
      color: rgba($color-cerulean-blue, 0.7);
    }

    .button__icon {
      color: $color-white;

      background-color: $color-cerulean-blue;
    }
  }
}

.button__text {
  color: $color-cerulean-blue;
  font-size: 14px;
  font-weight: 500;
  line-height: 100%;
  letter-spacing: 0.28px;
  text-transform: uppercase;
  text-align: center;

  transition: color $trans-default;

  flex-grow: 1;

  @include mobile {
    font-size: 12px;
    letter-spacing: 0.24px;
  }
}

.button__icon {
  display: flex;
  justify-content: center;
  align-items: center;

  color: $color-white;

  transition: background-color $trans-default, color $trans-default, transform $trans-default;
  background-color: $color-cerulean-blue;
  border-radius: 50%;

  flex-shrink: 0;
  width: 46px;
  height: 46px;

  @include mobile {
    width: 34px;
    height: 34px;
  }
}
